import React from "react";
import ProfileImageWithDefault from "./ProfileImageWithDefault";
import { Link } from "react-router-dom";
import { format } from "timeago.js";
import { useSelector } from "react-redux";
import { deleteSelale } from "../api/apiCalls";

const SelaleView = (props) => {
  const loggedInUser = useSelector((store) => store.username);
  const { selale, onDeleteSelale } = props;
  const { user, content, timeStamp, id } = selale;
  const { username, displayName, image } = user;

  const onClickDelete = async () => {
    await deleteSelale(id);
    onDeleteSelale(id);
  };

  const formatted = format(timeStamp);

  const ownedByLoggedInUser = loggedInUser === username;

  return (
    <div className="card p-1">
      <div className="d-flex">
        <ProfileImageWithDefault
          image={image}
          width="32"
          height="32"
          className="rounded-circle"
          style={{ margin: "2px" }}
        />
        <div className="flex-fill m-auto" style={{ paddingLeft: "5px" }}>
          <Link to={`/user/${username}`} className="text-dark">
            <h6 className="d-inline">
              {displayName}@{username}
            </h6>
          </Link>
          <span> - </span>
          <span>{formatted}</span>
        </div>
        {ownedByLoggedInUser && (
          <button className="btn btn-delete-link" onClick={onClickDelete}>
            <i className="material-icons">delete_outline</i>
          </button>
        )}
      </div>
      <div className="" style={{ paddingLeft: "40px" }}>
        {content}
      </div>
    </div>
  );
};

export default SelaleView;
