import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import ProfileImageWithDefault from "./ProfileImageWithDefault";
import { postSelale } from "../api/apiCalls";
import { useApiProgress } from "../shared/ApiProgress";
import ButtonWithProgress from "./ButtonWithProgress";

const SelaleSubmit = () => {
  const { image } = useSelector((store) => ({ image: store.image }));
  const [focused, setFocused] = useState(false);
  const [selale, setSelale] = useState("");
  const [errors, setErrors] = useState({});

  useEffect(() => {
    if (!focused) {
      setSelale("");
      setErrors({});
    }
  }, [focused]);

  useEffect(() => {
    setErrors({});
  }, [selale]);

  const pendingApiCall = useApiProgress("post", "/api/1.0/selales");
  const onClickSelale = async () => {
    const body = {
      content: selale,
    };
    try {
      await postSelale(body);
      setFocused(false);
    } catch (error) {
      if (error.response.data.validationErrors) {
        setErrors(error.response.data.validationErrors);
      }
    }
  };

  let textAreaClass = "form-control";
  if (errors.content) {
    textAreaClass += " is-invalid";
  }

  return (
    <div className="card p-2 flex-row">
      <ProfileImageWithDefault
        image={image}
        width="32px"
        height="32px"
        style={{ marginRight: "5px" }}
      />
      <div className="flex-fill">
        <textarea
          className={textAreaClass}
          rows={focused ? "5" : "1"}
          onFocus={() => setFocused(true)}
          onChange={(event) => setSelale(event.target.value)}
          value={selale}
        />
        <div className="invalid-feedback">{errors.content}</div>
        {focused && (
          <div className="text-right mt-2">
            <ButtonWithProgress
              className="btn btn-primary"
              onClick={onClickSelale}
              text="Comment"
              pendingApiCall={pendingApiCall}
              disabled={pendingApiCall}
            />
            <button
              className="btn btn-secondary d-inline-flex"
              style={{
                marginLeft: "5px",
              }}
              onClick={() => setFocused(false)}
            >
              <i className="material-icons">close</i>Cancel
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default SelaleSubmit;
