import React, { useState, useEffect } from "react";
import Spinner from "./Spinner";
import { getSelales } from "../api/apiCalls";
import { useApiProgress } from "../shared/ApiProgress";
import SelaleView from "./SelaleView";
import { useParams } from "react-router-dom";

const SelaleFeed = () => {
  const [selalePage, setSelalePage] = useState({
    content: [],
    last: true,
    number: 0,
  });
  const { username } = useParams();

  const path = username
    ? `/api/1.0/users/${username}/selales?page=`
    : "/api/1.0/selales?page=";
  const pendingApiCall = useApiProgress("get", path);

  useEffect(() => {
    loadSelales();
  }, []);

  const loadSelales = async (page) => {
    try {
      const response = await getSelales(username, page);
      setSelalePage((previousSelalePage) => ({
        ...response.data,
        content: [...previousSelalePage.content, ...response.data.content],
      }));
    } catch (error) {}
  };

  const onDeleteSelaleSuccess = (id) => {
    setSelalePage((previousSelalePage) => ({
      ...previousSelalePage,
      content: previousSelalePage.content.filter((selale) => selale.id !== id),
    }));
  };

  const { content, last, number } = selalePage;

  if (content.length === 0) {
    return (
      <div className="alert alert-secondary text-center">
        {pendingApiCall ? <Spinner /> : "There are no comment!"}
      </div>
    );
  }

  return (
    <div>
      {content.map((selale) => {
        return (
          <SelaleView
            key={selale.id}
            selale={selale}
            onDeleteSelale={onDeleteSelaleSuccess}
          />
        );
      })}
      {!last && (
        <div
          className="alert alert-secondary text-center"
          style={{ cursor: pendingApiCall ? "not-allowed" : "pointer" }}
          onClick={pendingApiCall ? () => {} : () => loadSelales(number + 1)}
        >
          {pendingApiCall ? <Spinner /> : "Load old comments :)"}
        </div>
      )}
    </div>
  );
};

export default SelaleFeed;
