import React, { useEffect, useState, useRef } from "react";
import logo from "../assets/commentse.png";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { logoutSuccess } from "../redux/authActions";
import ProfileImageWithDefault from "./ProfileImageWithDefault";

const TopBar = (props) => {
  const { username, isLoggedIn, displayName, image } = useSelector((store) => ({
    isLoggedIn: store.isLoggedIn,
    username: store.username,
    displayName: store.displayName,
    image: store.image,
  }));

  const menuArea = useRef(null);

  const [menuVisible, setMenuVisible] = useState(false);

  useEffect(() => {
    document.addEventListener("click", menuClickTracker);
    return () => {
      document.removeEventListener("click", menuClickTracker);
    };
  }, [isLoggedIn]);

  const menuClickTracker = (event) => {
    if (menuArea.current === null || !menuArea.current.contains(event.target)) {
      setMenuVisible(false);
    }
  };
  const dispatch = useDispatch();

  const onLogoutSuccess = () => {
    dispatch(logoutSuccess());
  };

  let links = (
    <div
      style={{
        display: "flex",
        flexDirection: "row",
        alignItems: "center",
      }}
    >
      <Link className="nav-link" to="/login">
        Login
      </Link>

      <Link className="nav-link" to="/signup">
        Sign Up
      </Link>
    </div>
  );
  if (isLoggedIn) {
    let dropDownClass = "dropdown-menu p-0 shadow";
    if (menuVisible) {
      dropDownClass += " show";
    }

    links = (
      <ul className="navbar-nav ml-auto" ref={menuArea}>
        <li className="nav-item dropdown">
          <div
            className="d-flex"
            style={{
              cursor: "pointer",
            }}
            onClick={() => {
              setMenuVisible(!menuVisible);
              console.log(menuVisible);
            }}
          >
            <ProfileImageWithDefault
              image={image}
              className="rounded-circle m-auto border"
              style={{
                width: "32px",
                height: "32px",
              }}
            />
            <span className="nav-link dropdown-toggle">{displayName}</span>
          </div>
          <div className={dropDownClass}>
            <Link
              className="dropdown-item d-flex p-2"
              to={`/user/${username}`}
              onClick={() => setMenuVisible(false)}
            >
              <i
                className="material-icons text-info"
                style={{ marginRight: "5px" }}
              >
                person
              </i>
              My Profile
            </Link>
            <span
              className="dropdown-item d-flex p-2"
              onClick={onLogoutSuccess}
              style={{ cursor: "pointer" }}
            >
              <i
                className="material-icons text-danger"
                style={{ marginRight: "5px" }}
              >
                power_settings_new
              </i>
              Logout
            </span>
          </div>
        </li>
      </ul>
    );
  }
  return (
    <div className="shadow-sm bg-light mb-3">
      <nav className=" navbar-light container navbar-expand">
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-between",
          }}
        >
          <div>
            <Link className="navbar-brand mr-50" to="/">
              <img
                src={logo}
                width="60"
                alt="CommentSelalesi Logo"
                style={{
                  border: "1px solid white",
                  borderRadius: "50%",
                }}
              />
              Comment Selalesi
            </Link>
          </div>
          {links}
        </div>
      </nav>
    </div>
  );
};

export default TopBar;
