import axios from "axios";

export const signup = (body) => {
  return axios.post("/api/1.0/users", body);
};

export const login = (creds) => {
  return axios.post("/api/1.0/auth", {}, { auth: creds });
};

export const getUsers = (page = 0, size = 3) => {
  return axios.get(`/api/1.0/users?page=${page}&size=${size}`);
};

export const setAuthorizationHeader = ({ username, password, isLoggedIn }) => {
  if (isLoggedIn) {
    const authorizationHeaderValue = `Basic ${btoa(username + ":" + password)}`;
    axios.defaults.headers["Authorization"] = authorizationHeaderValue;
  } else {
    delete axios.defaults.headers["Authorization"];
  }
};

export const getUser = (username) => {
  return axios.get(`/api/1.0/users/${username}`); //backtcik içinde atıyoruz çünkü parametrik bir requeset oluşturucaz
};

export const updateUser = (username, body) => {
  return axios.put(`/api/1.0/users/${username}`, body);
};

export const postSelale = (selale) => {
  return axios.post("/api/1.0/selales", selale);
};

export const getSelales = (username, page = 0) => {
  const path = username
    ? `/api/1.0/users/${username}/selales?page=`
    : "/api/1.0/selales?page=";
  return axios.get(path + page);
};

export const deleteSelale = (id) => {
  return axios.delete(`/api/1.0/selales/${id}`);
};
